@media screen and (max-width: 767px) {
  .at-disc .at-txt {
    padding: 30px 15px;
  }
}

.at-disc .at-price dt {
  padding: 50px 0 30px;
  text-align: center;
}

@media screen and (max-width: 767px) {
  .at-disc .at-price dt {
    padding: 30px 0;
  }
}

.at-disc .at-price dd {
  padding-bottom: 10px;
  font-size: 1.4rem;
  text-align: center;
}

/* --------------------------------
 * menulist
 * -------------------------------- */
.menulist .section-inner {
  justify-content: space-between;
}

.menus {
  padding: 0 0;
}

@media screen and (max-width: 767px) {
  .menus {
    padding: 0 15px;
    margin-bottom: 1px;
  }
}

.menus li {
  flex: 0 0 30%;
  padding: 8% 0 3%;
  border: 1px solid #707070;
}

@media screen and (max-width: 767px) {
  .menus li {
    flex: 1 1 0;
    padding: 15% 0 15%;
    margin-bottom: 30px;
  }
}

.menus .menulist-name {
  height: 50px;
  padding-bottom: 60%;
  text-align: center;
}

@media screen and (max-width: 767px) {
  .menus .menulist-name {
    padding-bottom: 30%;
  }
}

.menus .menulist-link {
  display: block;
  width: 60%;
  padding: 10px 0;
  margin: 0 auto;
  border: 1px solid #444444;
  text-align: center;
  font-size: 1.4rem;
  letter-spacing: 0.2em;
  color: #78CBF3;
  transition: .3s;
  position: relative;
  z-index: 2;
  overflow: hidden;
}

.menus .menulist-link:after {
  display: block;
  content: "";
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  margin: 0;
  border-radius: 50%;
  background: #FAFAFA;
  transition: .8s;
}

.menus .menulist-link:hover:after {
  width: 300px;
  height: 300px;
  margin: -150px 0 0 -150px;
}

@media screen and (max-width: 767px) {
  .menus .menulist-link {
    position: static;
    transform: translateY(0);
    font-size: 1.6rem;
  }
}
